@use 'base/variables' as v;
@use 'base/mixins' as m;

.sidebar{
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition: opacity 500ms;

    &.mostrar{
        z-index: 2;
        opacity: 1;
        padding: 2rem;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgb(0 0 0 /.85);
        @include m.tablet{
            display: none;
        }
    }
    &.ocultar{
        opacity: 0;
    }

    @include m.tablet{
        opacity: 1;
        height: 100%;
        padding: 2rem;
        background-color: v.$indigo;
    }

    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2{
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }
        .cerrar-menu{
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;
            img{
                filter: invert(100%);
                &:hover{
                    cursor: pointer;
                }
            }
            @include m.tablet{
                display: none;
            }
        }
    }

    .sidebar-nav{
        margin-top: 5rem;
        a{
            color: v.$blanco;
            display: block;
            padding: 0.7rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-transform: uppercase;
            font-weight: bold;
            font-style: 2.4rem;
            margin-bottom: 2rem;
            text-align: center;

            &:hover,
            &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }
            @include m.tablet{
                text-align: left;
                font-style: 1.2rem;
                margin-bottom: 1.5rem;
            }
        }
    }

    .cerrar-sesion-mobile{
        margin-top: 10rem;
        .cerrar-sesion{
            @include m.boton(v.$indigo, v.$blanco);
        }
        @include m.tablet{
            display: none;
        }
    }
}